import React from "react";
import "../translations/i18n";
import Timeline from "@material-ui/lab/Timeline";
import TimelineItem from "@material-ui/lab/TimelineItem";
import TimelineSeparator from "@material-ui/lab/TimelineSeparator";
import TimelineConnector from "@material-ui/lab/TimelineConnector";
import TimelineContent from "@material-ui/lab/TimelineContent";
import TimelineOppositeContent from "@material-ui/lab/TimelineOppositeContent";
import TimelineDot from "@material-ui/lab/TimelineDot";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { useTranslation } from "react-i18next";
import { Container } from "@material-ui/core";

export const Experiences = () => {
  const { t } = useTranslation();

  return (
    <Container maxWidth={false}
               disableGutters={true}
               className="container">
      <h1 className="container-title">{t("common:workExperienceTitle")}</h1>
      <Timeline>
        {displayRoles()}
      </Timeline>
    </Container>
  );
};

const displayRoles = () => {
  const { t } = useTranslation();

  const profileData: any = t("profileData:profile", { returnObjects: true });

  const timelineItems: any[] = [];

  profileData.roles.map(
    (role: any) =>
      timelineItems.push(timelineItem(
        dateToString(role.startContractDate),
        dateToString(role.endContractDate),
        role.position,
        role.jobDescription
      ))
  );

  return (
    <div>
      {timelineItems}
    </div>
  );
};

const timelineItem = (
  startDate: string,
  endDate: string,
  position: string,
  jobDescription: string
) => {
  return (
    <TimelineItem>
      <TimelineOppositeContent
        style={{flex: "0",
        minWidth: "100px"}}
        className={"timeline-opposite-content"}>
        <Typography variant="body2" color="textSecondary">
          {startDate}
          <br />
          to
          <br />
          {endDate}
        </Typography>
      </TimelineOppositeContent>
      <TimelineSeparator>
        <TimelineDot className="experience-timelinedot" color={"inherit"} />
        <TimelineConnector />
      </TimelineSeparator>
      <TimelineContent>
        <Paper elevation={3} style={{ padding: "6px 16px" }}>
          <Typography variant="h6" component="h1">
            {position}
          </Typography>
          <Typography>
            {jobDescription}
          </Typography>
        </Paper>
      </TimelineContent>
    </TimelineItem>
  );
};

const dateToString = (date: string): string => {
  if (date != undefined) {
    const formatedDate: Date = new Date(date);
    return formatedDate.getFullYear() + " " +
      formatedDate.toLocaleString('default', { month: 'short' });
  } else {
    return "Today";
  }
};




